.service__main {
  height: 210px;
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 16px;
  overflow: hidden; }

.service__list {
  display: flex;
  min-width: 0;
  height: 160px; }
  .service__list-item {
    width: 224px; }

.service__button-service {
  font-family: 'TT Lakes-Regular';
  font-size: 16px;
  line-height: 24px;
  position: relative;
  text-align: start;
  width: 100%;
  height: 160px;
  background-color: white;
  border: 1px solid #eaeaea;
  border-radius: 6px;
  display: flex;
  padding-top: 16px;
  padding-left: 16px;
  margin: 0;
  box-sizing: border-box; }
  .service__button-service::after {
    content: '';
    position: absolute;
    width: 40px;
    height: 40px;
    bottom: 6px;
    right: 20px;
    border: 2px solid #ff3e79;
    border-radius: 50%; }
  .service__button-service::before {
    content: '';
    position: absolute;
    bottom: 20px;
    right: 34px;
    background: url(./static/arrow_right.svg) no-repeat;
    width: 8px;
    height: 12px; }

@media all and (min-width: 768px) {
  .service .section-title {
    background: linear-gradient(90deg, #f8f8f8 1.39%, #ffffff 100%); }
  .service__main {
    height: auto; }
  .service__list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(224px, 1fr));
    gap: 24px;
    overflow: hidden; }
    .service__list--visible {
      height: auto;
      overflow: visible; }
  .service .swiper {
    width: auto;
    height: auto; }
  .service .swiper-pagination {
    display: none; }
  .service .read-more__button-text--rot::before {
    transform: rotate(180deg);
    transition: transform 0.5s; } }

@media all and (min-width: 1336px) {
  .service__main {
    padding-left: 32px; } }
