.footer {
  display: flex;
  flex-direction: column;
  gap: 16px;
  color: #7e7e82;
  border-top: 1px solid #d9fff5;
  padding-top: 16px; }
  .footer__text {
    text-align: center; }
  @media all and (min-width: 768px) {
    .footer {
      flex-direction: row;
      justify-content: space-between;
      gap: 36px;
      padding: 32px 24px; }
      .footer__text:first-child {
        width: 250px;
        height: 32px;
        text-align: left; }
      .footer__text:last-child {
        width: 240px;
        height: 48px;
        text-align: right; } }
