.price {
  margin-bottom: 40px; }
  .price__main {
    padding: 8px 16px;
    height: 240px;
    box-sizing: border-box; }
  .price__table-header {
    display: none; }
  .price__list {
    display: flex;
    height: 200px;
    margin-bottom: 16px; }
    .price__list-item {
      display: flex;
      position: relative;
      padding: 16px;
      flex-direction: column;
      width: 260px;
      background-color: white;
      border-radius: 6px;
      box-sizing: border-box; }
      .price__list-item-text {
        font-size: 14px; }
        .price__list-item-text-name {
          line-height: 16px;
          margin-bottom: 4px;
          color: #7e7e82; }
        .price__list-item-text-description {
          line-height: 18px;
          margin-bottom: 16px; }
      .price__list-item-flex {
        display: flex;
        justify-content: space-between; }
  .price__button-price {
    display: flex;
    position: absolute;
    font-family: 'TT Lakes-Regular';
    width: 119px;
    height: 32px;
    right: 8px;
    bottom: 16px;
    font-size: 12px;
    padding: 4px 12px;
    color: white;
    background-color: #ff3e79;
    border: 0px;
    border-radius: 16px;
    justify-content: flex-start;
    align-items: center;
    text-transform: uppercase;
    font-weight: bold; }
    .price__button-price::after {
      content: '';
      position: absolute;
      bottom: 10px;
      right: 10px;
      width: 8px;
      height: 10px;
      background: url(./static/arrow_right-white.svg); }
  .price__text {
    padding: 0px 16px;
    font-size: 14px;
    line-height: 20px;
    max-width: 450px;
    height: auto;
    letter-spacing: 0.2px; }
  .price__button-offer {
    position: relative;
    font-family: 'TT Lakes-Regular';
    font-size: 16px;
    line-height: 24px;
    padding-left: 16px;
    padding-top: 10px;
    padding-bottom: 40px;
    width: 240px;
    height: 54px;
    border: 0px;
    background-color: #f8f8f8;
    text-align: start;
    font-weight: bold;
    cursor: pointer; }
    .price__button-offer::before {
      content: '';
      position: absolute;
      bottom: -13px;
      left: 130px;
      background: url(./static/arrow_right.svg) no-repeat;
      width: 24px;
      height: 26px; }
  @media all and (min-width: 768px) {
    .price__table-header {
      display: flex;
      align-self: center;
      padding-left: 20px;
      padding-bottom: 16px;
      padding-top: 31px; }
      .price__table-header-service {
        width: 348px; }
      .price__table-header-price {
        width: 74px;
        margin-left: 24px; }
      .price__table-header-date {
        width: 84px;
        margin-left: 24px; }
    .price .swiper {
      width: auto;
      height: auto; }
    .price .swiper-pagination {
      display: none; }
    .price__list {
      display: grid;
      border-radius: 6px;
      width: auto;
      height: auto; }
      .price__list-item {
        border-radius: 0px;
        flex-direction: row;
        align-items: center;
        align-self: center;
        width: auto; }
        .price__list-item-text-description {
          margin-bottom: 0px; }
        .price__list-item + .price__list-item {
          border-top: 1px solid #eaeaea; }
    .price__button-price {
      bottom: 8px; }
    .price__container-service, .price__container-description {
      width: 348px; }
    .price__container-price, .price__container-cost {
      width: 74px;
      margin-left: 24px; }
    .price__container-date, .price__container-time {
      width: 84px;
      margin-left: 24px; }
    .price__container-service, .price__container-price, .price__container-date {
      display: none; } }
  @media all and (min-width: 1336px) {
    .price__main {
      padding: 8px 32px; }
    .price__text {
      padding-left: 32px; }
    .price__button-offer {
      padding-left: 32px; }
      .price__button-offer::before {
        left: 140px; } }
